.img {
    max-width: 300px;
    width: 80%;
}

.img img {
    width: 100%;
    height: auto;
}

.wrap {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background: #44464a;
    color: #e0e0e0;
}

.contents {
    text-align: center;
    margin: 20px;
}

.contents * {
    margin: 0;
    padding: 5px 0;
    font-weight: normal;
}

.contents h1 {
    font-size: 3rem;
}

.footer {
    position: fixed;
    bottom: 20px;
    color: #949494;
}